<script>
import Header from "../../components/global/header/HeaderArea.vue"
import CitySelecter from "./components/CitySelecter.vue"
import { ref, onMounted, watch } from 'vue'
import { Toast } from '../../components/global/swal.js'
import { apiRehabus, apiRehabusAllCity } from "@/api/api"

export default {
  setup() {
    let rehabusData = ref([])
    let cityAreaData = ref([])
    let city = ref("請選擇縣市")
    let area = ref("請選擇鄉鎮市區")
    let cityIdx = ref(0)
    let areaIdx = ref(0)

    const updateCityIdx = (val) => {
      cityIdx.value = val.value
    }
    const updateAreaIdx = (val) => {
      areaIdx.value = val.value
    }

    watch(() => cityIdx.value, async () => {
      rehabusData.value = await apiRehabusAllCity({ cityId: cityIdx.value })
      rehabusData.value = rehabusData.value.data
    })
    watch(() => areaIdx.value, async () => {
      rehabusData.value = await apiRehabus({ cityId: cityIdx.value, areaId: areaIdx.value })
      if (rehabusData.value.data.length == 0) {
        Toast("該區域無資料")
        rehabusData.value = []
      } else {
        rehabusData.value = rehabusData.value.data
      }
    })
    return { rehabusData, updateCityIdx, updateAreaIdx }
  },
  components: {
    Header, CitySelecter
  },
}
</script>

<template>
  <Header title="復康巴士" />
  <div class="rehabus">
    <section class="c-main">
      <CitySelecter @city-idx="updateCityIdx" @area-idx="updateAreaIdx" />
      <div class="resource-container">
        <div class="list-group">
          <div v-for="item in rehabusData" :key="item.id">
            <a :href=item.website class="list-group-item list-group-item-action">{{ item.name }}<span
                v-if="item.area.length == 1">({{ item.area[0].name }})</span></a>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.rehabus {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}
</style>